
.emovideo {
  padding-top: 40px;
  padding-bottom: 40px;
  width: 100%;
  background-color: #1d1d1d;
  justify-content: center;
  display: flex;
}

.girls_collection_banner {
  width: 100%;

  img {
    width: 100%;
    height: 100%;
  }
}

.player_wrapper {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  padding-top: 56.25%;
}

.video__container {
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  width: 100% !important;
  height: 100% !important;
}

.video-stream.html5-main-video{
  width: 100%;
}

.react-player > video {
  position: absolute;
  object-fit: fill;
}

